
.calendar-wrap
{
	position: relative;
	top: 100%;
}

.calendar {
  position: absolute;
  z-index: 5;
  background-color: #fff;
  box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.4);
  border-radius: 0 0 0.5rem 0.5rem;
}
.calendar-header {
  background-color: #0075BF;
  color: #fff;
  padding: 1.5rem;
  font-weight: 300;
  height: 6rem;
  width: 24rem;
}
.calendar-year {
  font-size: 0.86rem;
  line-height: 0.86rem;
  margin-bottom: 0.5rem;
}
.calendar-date {
  font-size: 1.875rem;
  line-height: 1.875rem;
}
.calendar-controls {
  font-size: 1.5rem;
  font-weight: 200;
  padding: 2rem;
  height: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #0075BF;
}
.calendar-controls button {
    border: none;
    cursor: pointer;
    background-color: transparent;
    outline: none;
}
.calendar-weekdays {
  font-size: 1rem;
  line-height: 1rem;
  color: #0075BF;
  padding: 0 1.5rem;
  display: flex;
}
.calendar-weekday {
  width: 3rem;
  text-align: center;
}
.calendar-days {
  padding: 0 1.5em;
  height: 15rem;
  width: 24rem;
  display: flex;
  flex-wrap: wrap;
}
.calendar-day {
  position: relative;
  width: 3rem;
  height: 3rem;
  line-height: 3rem;
  cursor: pointer;
  text-align: center;
}
.calendar-day-text {
  position: relative;
  z-index: 2;
}
.calendar-day-effect {
  position: absolute;
  left: 0.25rem;
  top: 0.25rem;
  z-index: 0;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  background-color: #3499D9;
  transition: all 450ms cubic-bezier(0.14, 0.82, 0.4, 0.94);
  transform: scale(0);
  opacity: 0;
}
.calendar-day:hover {
  color: #fff;
}
.calendar-day:hover .calendar-day-effect {
  transform: scale(1);
  opacity: 0.6;
}
.calendar-day.selected {
  color: #fff;
}
.calendar-day.selected .calendar-day-effect {
  transform: scale(1);
  background-color: #0075BF;
  opacity: 1;
}
.calendar-actions {
  padding: 0 1rem;
  display: flex;
  justify-content: flex-end;
}
.calendar-actions button {
    border: none;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    font-size: 0.86rem;
    font-weight: 500;
    text-transform: uppercase;
    min-width: 6rem;
    padding: 1rem;
    transition: all 0.3s;
}
.calendar-actions button:hover {
      background-color: #F4F4F4;
}
.calendar-actions .cancel {
    color: #8795a1;
}
.calendar-actions .sub {
    color: #0075BF;
}
.calendar-actions .slide-enter-active, .calendar-actions .slide-leave-active {
    transition: opacity .5s;
}
.calendar-actions .slide-enter, .calendar-actions .slide-leave-to {
    opacity: 0;
}
.options {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: ceneter;
}
timepicker {
  position: relative;
  border-top: 1px solid #3499D9;
  height: 4em;
  width: 100%;
}

.timepicker {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 3rem;
  position: relative;
  margin: 1em 0;
}
.timepicker button {
    border: none;
    outline: none;
    background-color: transparent;
}
.timepicker input {
    outline: none;
    border: none;
    text-align: center;
    font-size: 1rem;
    width: 50%;
    height: 100%;
}
.halfday-timepicker button {
  width: 100%;
  color: #8795a1;
  position: relative;
  z-index: 5;
  font-size: 1rem;
  font-weight: 300;
  cursor: pointer;
}
.halfday-timepicker button.active {
  color: white;
}
.halfday-timepicker .halfday-switch {
  position: absolute;
  z-index: 4;
  height: 100%;
  width: 50%;
  background-color: #3499d9;
  transform: translateX(-50%);
  transition: all 300ms cubic-bezier(0.17, 0.67, 0.54, 0.91);
}
.halfday-timepicker .halfday-switch.active {
  transform: translateX(50%);
}

