.hour-timepicker .hour {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.hour-timepicker .hour .timepicker-controls {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    opacity: 0;
}
.hour-timepicker .hour .timepicker-controls img {
      height: 0.75rem;
      cursor: pointer;
}
.hour-timepicker .hour span {
    color: #8795a1;
}
.hour-timepicker .hour input {
  width: 70%;
}
.hour-timepicker .hour .timepicker-controls {
  right: 1rem;
}
.hour:hover .timepicker-controls {
  opacity: 1;
}

